import { useState } from "react";
import {
  FaUserTie,
  FaFileAlt,
  FaHandshake,
  FaUsers,
  FaClock,
  FaDollarSign,
} from "react-icons/fa";

export default function Home() {
  const [data] = useState({
    name: "Abdelrahman Mabrouk",
    title: "Sales Representative",
    territory: "Global",
    monthlyTarget: 166667,
    avgDealSize: 2406624,
    yearTarget: 2000000,
    experience: 0,
    achieved: 0,
    pipelineValue: 9626496,
    conversionRate: 7.69,
    followUps: 0,
    achievement: 0,
    weather: 25,
    leads: 6,
    opportunities: 23,
    quotations: 13,
    closedDeals: 1,
    invoices: 0,
    customers: 8,
    followUpsToday: 0,
  });

  const formatSAR = (num) => `SAR ${num.toLocaleString()}`;

  return (
    <div className="min-h-screen bg-gray-50 text-gray-800 font-sans">
      {/* Header */}
      <header className="flex items-center justify-between px-6 py-3 bg-white shadow">
        <div className="flex items-center space-x-3">
          <img src="/logo192.png" alt="CRM" className="w-6 h-6" />
          <h1 className="font-semibold text-lg">CRM</h1>
          <nav className="flex space-x-4 text-sm text-gray-600 ml-6">
            <span>Sales Dashboard</span>
            <span>Leads</span>
            <span>Opportunities</span>
            <span>Quotations</span>
            <span>Sale Orders</span>
            <span>Invoices</span>
            <span>Customers</span>
            <span>My Activities</span>
            <span>Reporting</span>
          </nav>
        </div>
        <div className="flex items-center space-x-4">
          <span className="font-medium text-gray-700">RIYADH HOUSE</span>
          <FaUserTie className="text-gray-600 w-6 h-6" />
        </div>
      </header>

      {/* Main Content */}
      <main className="p-6">
        {/* Profile + Stats */}
        <div className="bg-white shadow rounded-2xl p-6 flex flex-col lg:flex-row justify-between items-start lg:items-center mb-6">
          <div className="flex items-center space-x-4">
            <div className="w-20 h-20 flex items-center justify-center rounded-lg bg-purple-100">
              <FaUserTie className="text-purple-500 text-5xl" />
            </div>
            <div>
              <h2 className="text-2xl font-bold text-blue-600">{data.name}</h2>
              <p className="text-gray-600">{data.title}</p>
            </div>
          </div>

          {/* Stats Grid */}
          <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-6 gap-x-10 gap-y-2 mt-4 lg:mt-0 text-sm">
            <Stat label="Territory" value={data.territory} />
            <Stat label="Monthly Target" value={formatSAR(data.monthlyTarget)} />
            <Stat label="Avg Deal Size" value={formatSAR(data.avgDealSize)} />
            <Stat label="Year Target" value={formatSAR(data.yearTarget)} />
            <Stat label="Experience" value={data.experience} />
            <Stat label="Conversion Rate" value={`${data.conversionRate}%`} />
            <Stat label="Pipeline Value" value={formatSAR(data.pipelineValue)} />
            <Stat label="Follow-ups" value={data.followUps} />
            <Stat label="Achievement" value={`${data.achievement}%`} />
          </div>
        </div>

        {/* Bottom Stats Boxes */}
        <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-6 gap-6">
          <StatBox color="green" icon={<FaFileAlt />} title="Leads" value={data.leads} />
          <StatBox color="amber" icon={<FaFileAlt />} title="Opportunities" value={data.opportunities} />
          <StatBox color="blue" icon={<FaFileAlt />} title="Quotations" value={data.quotations} />
          <StatBox color="yellow" icon={<FaHandshake />} title="Closed Deals" value={data.closedDeals} />
          <StatBox color="gray" icon={<FaDollarSign />} title="Invoices" value={data.invoices} />
          <StatBox color="purple" icon={<FaUsers />} title="Customers" value={data.customers} />
        </div>

        <div className="mt-6 grid grid-cols-1 md:grid-cols-6">
          <StatBox color="orange" icon={<FaClock />} title="Follow-ups Today" value={data.followUpsToday} />
        </div>
      </main>
    </div>
  );
}

function Stat({ label, value }) {
  return (
    <div>
      <p className="font-semibold">{label}:</p>
      <p>{value}</p>
    </div>
  );
}

function StatBox({ color, icon, title, value }) {
  const colorMap = {
    green: "bg-green-100 text-green-600",
    amber: "bg-amber-100 text-amber-600",
    blue: "bg-blue-100 text-blue-600",
    yellow: "bg-yellow-100 text-yellow-600",
    gray: "bg-gray-100 text-gray-600",
    purple: "bg-purple-100 text-purple-600",
    orange: "bg-orange-100 text-orange-600",
  };
  return (
    <div className="bg-white shadow rounded-2xl p-5 flex flex-col items-center justify-center text-center">
      <div className={`p-3 rounded-full ${colorMap[color]} text-2xl mb-3`}>
        {icon}
      </div>
      <p className="text-2xl font-bold">{value}</p>
      <p className="text-gray-600 text-sm">{title}</p>
    </div>
  );
}
